.tui-pagination * {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
    overflow: hidden;
}

.tui-pagination {
    height: 40px;
    margin: 60px 0 0;
    line-height: normal;
    text-align: center;
    font-size: 0;
}

.tui-pagination .tui-page-btn {
    display: inline-block;
    position: relative;
    width: 40px;
    height: 40px;
    padding: 14px 0 14px;
    margin-left: 0;
    color: #000;
    font-size: 12px;
    /* font-weight: normal; */
    line-height: 1;
    text-decoration: none;
    vertical-align: middle;
    /* border: 1px solid #ddd; */
    /* border-width: 1px 0; */
}

.tui-pagination .tui-first,
.tui-pagination .tui-last {
    display: none;

    @media screen and(min-width: 480px) {
        /* 768px */
        display: inline-block;
    }
}

.tui-pagination .tui-page-btn:hover {
    background-color: #f4f4f4;
    border-radius: 5px;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
    color: #fff;
    background: #B92F2C;
    border-color: #B92F2C;
    cursor: default;
    border-radius: 5px;
}

.tui-pagination .tui-is-selected:hover {
    background-color: #B92F2C;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
    margin-left: 0
}

.tui-pagination .tui-first-child,
.tui-pagination .tui-prev-is-ellip {
    border-left: none;
}

.tui-pagination .tui-last-child,
.tui-pagination .tui-next-is-ellip {
    border-right: none;
}

.tui-pagination .tui-first-child.tui-is-selected {
    border-left: 1px solid #B92F2C;
}

.tui-pagination .tui-last-child.tui-is-selected {
    border-right: 1px solid #B92F2C;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    overflow: hidden;
    height: 40px;
    padding: 0;
    font-size: 0;
    line-height: 40px;
    white-space: nowrap;
    /* font-weight: normal; */
    /* border: 1px solid #ddd; */
}

.tui-pagination .tui-prev-is-ellip {
    border-right: 0
}

.tui-pagination .tui-next-is-ellip {
    border-left: 0
}

.tui-pagination .tui-first+.tui-prev,
.tui-pagination .tui-next+.tui-last {
    border-left: 0,
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
    background: url(data:image/png;base64,) no-repeat 0 0;
    display: inline-block;
    vertical-align: middle;
    font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    padding: 4px;
    line-height: 1;
}

.tui-pagination .tui-prev {
    margin-right: 0;
    /* background-color: #F7F7F7;
    border-radius: 5px; */
}

.tui-pagination .tui-next {
    margin-left: 0;
    /* background-color: #F7F7F7;
    border-radius: 5px; */
}

.tui-pagination .tui-ico-first {
    width: 11px;
    height: 10px;
    background-position: 0px 0px,
}

.tui-pagination .tui-ico-prev {
    width: 6px;
    height: 10px;
    background-position: -26px 0px
}

.tui-pagination .tui-ico-next {
    width: 6px;
    height: 10px;
    background-position: -13px -24px
}

.tui-pagination .tui-ico-last {
    width: 11px;
    height: 10px;
    background-position: 0px -12px
}

.tui-pagination .tui-ico-ellip {
    width: 10px;
    height: 2px;
    margin-top: 16px;
    vertical-align: top;
    background-position: 0px -24px
}

.tui-pagination .tui-is-disabled:hover {
    background: #fff;
    cursor: default
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
    background-position: -26px -12px
}

.tui-pagination .tui-is-disabled .tui-ico-first {
    background-position: -13px 0px
}

.tui-pagination .tui-is-disabled .tui-ico-next {
    background-position: -21px -24px
}

.tui-pagination .tui-is-disabled .tui-ico-last {
    background-position: -13px -12px
}

.tui-page-inputs {
    display: inline-block;
    vertical-align: top;
}

.tui-page-inputs .tui-page-input {
    display: inline-block;
    width: 10px;
    height: 12px;
    padding: 14px 0 14px;
    font-size: 12px;
    text-align: center;
    color: #000;
    border: 0;
    outline: none;
}

.tui-page-inputs .tui-page-current {
    color: #B92F2C
}

.tui-page-separator {
    display: inline-block;
    font-size: 12px;
    height: 12px;
    padding: 14px 0 14px;
    vertical-align: top
}

